import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { NgForm, NgModel } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-add-contact',
  templateUrl: './add-contact.component.html',
  styleUrls: ['./add-contact.component.css']
})

export class AddContactComponent  {
  data : object;
  formArr: Array<object>
  params: object;
  constructor(private route: Router, public http:HttpClient) { 
    this.formArr = []
  }

 getData(sendD:string):Observable<any> {
      return this.http.get(sendD);
    }

    redirect(){
    this.route.navigate(['//'])
      }
  addContact(data:NgForm){
      
    if (!data.valid) {
      console.log("error");
    }
    else
    {
      console.log("Form Submitted!");
      console.log(data.value)
      this.route.navigate(['//'],{ queryParams: data.value ,queryParamsHandling: 'merge' })
      this.formArr.push(data.value)
      console.log(this.formArr)
      console.log(this.formArr[0]["firstName"][0])
    }
     console.log("values"+ data.value);

  }
 
  // clearPosParam() {
  //   this.router.navigate(
  //     ['.'], 
  //     { relativeTo: this.route, queryParams: { this } }
  //   );
  // }
}



