import { Component } from '@angular/core';
import {QueryService} from '../query.service';
import { AddContactComponent } from '../add-contact/add-contact.component';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-contactlist',
  templateUrl: './contactlist.component.html',
  styleUrls: ['./contactlist.component.css']
})
export class ContactlistComponent  {
  contacts:Object;
  latestContact:Object;
  contactsArr: Array<object>;
  recentContactsArr: Array<object>;
  AlphapaticArr: Array<any>;
  data: object;
  formAdd: Array<object>
  constructor(private q:QueryService , private c: ActivatedRoute) { 
    this.formAdd= []
    this.c.queryParams.subscribe( params => 
      this.formAdd.push(params)

    )
    this.latestContact={};
    this.data = {};
    this.contactsArr =[];
    this.recentContactsArr =[];
    this.AlphapaticArr =[
      'a' ,'b', 'c'  ,'d' ,'e','f','g','h','i','j',' k','l',
      'm','n', 'o',' p','q','r','s',' t','u', 'v','w','x','y', 'z'
    ];
    this.getContactData();
    this.getRecentContactData();
  }  
  getContactData():void{
    let path:string='././assets/contacts.json';
    this.q.getData( path ).subscribe(
      res => {
        this.contacts=res; 
     for (let iterator in this.contacts) {
      if( this.contacts[iterator].constructor === Array)
        {
          this.contactsArr= this.contacts[iterator]
        }
      }
      for (let user = 0; user < this.contactsArr.length ; user++){
        if( this.contactsArr[user]['firstName']){
            let fChar = this.contactsArr[user]['firstName'].toLowerCase()[0];
              if(this.data[fChar])
              {        
                this.data[fChar].push(this.contactsArr[user]);
              }
              else{
                this.data[`${fChar}`] = [] ;
                this.data[fChar].push(this.contactsArr[user]);
              }      
      }

    }
    for (const key in this.data) {
      if(this.formAdd[0]['firstName'][0].toLowerCase() == key)  
      {
        this.data[key].push(this.formAdd[0])
      }      
}

      },
      err => {console.log(err);},
    );
  }

  getRecentContactData():void{
    let path:string='././assets/recent-contact.json';
    this.q.getData( path ).subscribe(
      res => {
        this.latestContact=res; 
     for (let iterator in this.latestContact) {
      if( this.latestContact[iterator].constructor === Array)
        {
          this.recentContactsArr= this.latestContact[iterator]
        }
      }
      console.log(this.recentContactsArr)
      },
      err => {console.log(err);},
    );
  }

}







